export default {
  //  cookie
  setCookie (value, days, name) {
    name = name || 'topenid'
    let d = new Date()
    d.setTime(d.getTime() + (days * 24 * 60 * 60 * 1000))
    let expires = 'expires=' + d.toUTCString()
    document.cookie = name + '=' + value + ';expires=' + expires
  },
  // type cookie
  getCookie (name) {
    let v
    name = name || 'topenid'
    v = window.document.cookie.match('(^|;) ?' + name + '=([^;]*)(;|$)') // cookie
    return v ? v[2] : null
  },
  // url
  getQueryString (name) {
    let reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)', 'i')
    let r = window.location.search.substr(1).match(reg)
    if (r !== null) {
      return unescape(r[2])
    }
    return null
  }
}
