import wx from 'weixin-js-sdk'
import {
  utils,
  apis,
  chttp
} from '@/libs/interfaces'
import store from '@/store'
import Cookies from 'js-cookie'
import Vue from 'vue'
const weixin = {
  // wxLoginUrl: '', // url
  wxLoginUrl (page, query) {
    const appid = process.env.VUE_APP_APPID
    let queryString = ''
    for (const key in query) {
      queryString += key + '=' + query[key] + '&'
    }
    return 'https://open.weixin.qq.com/connect/oauth2/authorize?appid=' + appid + '&redirect_uri=&response_type=code&scope=snsapi_userinfo&state=/' + page + '?' + queryString + '&connect_redirect=1#wechat_redirect'
  },
  // 
  getOpenID (page, query, callback) {
    // openid
    let openid = utils.util.getUrlPrem('openid') || Cookies.get('openid') || store.state.openid
    // unionidID
    let unionid = utils.util.getUrlPrem('unionid') || Cookies.get('unionid') || store.state.unionid
    if (openid && unionid) {
      Cookies.set('openid', openid, {
        expires: 30
      })
      Cookies.set('unionid', unionid, {
        expires: 30
      })
      // utils.cookie.setCookie(openid, 30)
      // utils.cookie.setCookie(unionid, 30, 'tunionid')
      store.commit('SAVE_OPENID', openid)
      store.commit('SAVE_UNIONID', unionid)
    }
  },
  isWxWebView () {
    const arr = window.navigator.userAgent.toLowerCase().match(/MicroMessenger/i)
    if (arr && arr.length > 0 && arr[0] === 'micromessenger') {
      return true
    } else {
      return false
    }
  },
  isXcxWebView () {
    return utils.util.getUrlPrem('browser') === 'xcx' || window.__wxjs_environment === 'miniprogram'
  },
  init (callback) {
    let req = {
      // 'action': 'js_signature',
      'type': 'thunder',
      'url': window.location.href.replace(/#.*$/, '')
    }
    // http.get(`${apis.initWx}?param=` + encodeURIComponent(JSON.stringify(req))).then(function (res) {
    chttp.get(apis.initWx, {
      params: req
    }).then(function (res) {
      try {
        wx.config({
          debug: false,
          appId: res.appId,
          timestamp: parseInt(res.timestamp),
          nonceStr: res.nonceStr,
          signature: res.signature,
          jsApiList: [
            'checkJsApi',
            'onMenuShareTimeline',
            'onMenuShareAppMessage',
            'chooseImage',
            'uploadImage',
            'scanQRCode',
            'startRecord',
            'stopRecord',
            'chooseWXPay',
            'getLocation',
            'openLocation',
            'getLocalImgData'
          ]
        })
        wx.ready(function () {
          wx.checkJsApi({
            jsApiList: [
              'chooseImage',
              'scanQRCode',
              'chooseWXPay',
              'getLocation',
              'getLocalImgData'
            ],
            success: function (res) {
              callback && callback()
            },
            failed: function (err) {
              console.log('', err)
            },
            complete: function () { }
          })
        })
        wx.error(res => {
        })
      } catch (g) {
      }
      let loading = document.getElementById('loading')
      loading.style.display = 'none'
      store.commit('CHANGE_HOME_BOTTOM_AD', true)
    }).catch(function (error) {
      console.log(error)
    })
  },
  updateShare (conf) {
    let settings = 'onMenuShareTimeline onMenuShareAppMessage'
    settings.split(' ').forEach(e => {
      try {
        wx[e](conf)
      } catch (g) {
        alert(g)
      }
    })
  },
  shareTimeLine (config, callback) {
    wx.onMenuShareTimeline({
      title: config.title, // 
      link: config.link || window.location.href, // 
      imgUrl: config.imgUrl, // 
      desc: config.desc,
      success: function () {
        callback && callback()
        // 
      },
      cancel: function () {
        // 
      }
    })
  },
  shareAppMessage (config, callback) {
    wx.onMenuShareAppMessage({
      title: config.title, // 
      desc: config.desc, // 
      link: config.link || window.location.href, // 
      imgUrl: config.imgUrl, // 
      type: '', // ,musicvideolinklink
      dataUrl: '', // typemusicvideo
      success: function () {
        callback && callback()
        // 
      },
      cancel: function () {
        // 
      }
    })
  },
  previewImage (current, urls) {
    wx.previewImage({
      current: current, // http
      urls: urls // http
    })
  },
  chooseImage (num, callback) {
    let localIds
    wx.chooseImage({
      count: num, // 9
      sizeType: ['compressed'], // 
      sourceType: ['album', 'camera'], // 
      success (res) {
        // callback && callback(res)
        localIds = res.localIds ? res.localIds : []
        uploadToWeiXin(localIds, callback)
      }
    })
    function uploadToWeiXin (ids, callback) {
      if (!ids.length) {
        return false
      }
      let length = ids.length
      let serverIds = []
      let i = 0
      upload()
      function upload () {
        wx.uploadImage({
          localId: ids[i],
          success: function (res) {
            serverIds.push(res.serverId)
            // if (num === 1 && localIds.length > 1) {
            // localIds.shift()
            // serverIds.shift()
            // }
            i++
            if (i < length) {
              upload()
            } else {
              callback && callback(serverIds)
            }
          },
          fail: function (res) {
            console.log(JSON.stringify(res))
          }
        })
      }
    }
  },
  uploadedImgs: [],
  uploadSuccessed: true,
  uploadImages (localIds, callback) {
    if (!localIds.length) return
    let localId = localIds.pop()
    let _this = this
    if (!_this.callback) {
      _this.callback = callback
    }
    wx.uploadImage({
      localId: localId, // IDchooseImage
      isShowProgressTips: 1, // 1
      success (res) {
        let serverId = res.serverId
        _this.uploadedImgs.push(serverId)
        if (res.errMsg !== 'uploadImage:ok') {
          _this.uploadSuccessed = false
        }
        if (localIds.length > 0) {
          _this.uploadImages(localIds)
        } else {
          if (!_this.uploadSuccessed) return
          try {
            _this.callback(_this.uploadedImgs)
          } catch (e) {
            alert(e)
          }
        }
      }
    })
  },
  getLocalImgData (i, localIds, callback) {
    wx.getLocalImgData({
      localId: localIds[i],
      success: function (res) {
        callback && callback(res)
      },
      fail: function (res) {
        console.log(res)
      }
    })
  },
  uploadImgsFn (num, callback) {
    let _this = this
    // 
    _this.chooseImage(num, (chooseRes) => {
      if (chooseRes.errMsg === 'chooseImage:ok') {
        _this.uploadImages(
          chooseRes.localIds,
          (uploadImages) => {
            _this.imgServerDownload(uploadImages, (downloadRes) => {
              callback(downloadRes)
            })
          })
      }
    })
  },
  imgServerDownload (uploadImages, callback) {
    // 
  },
  scanQRCode (callback, type) {
    wx.scanQRCode({
      desc: 'scanQRCode desc',
      needResult: type === 1 ? type : 0, // 01
      scanType: ['qrCode', 'barCode'], // 
      success: function (res) {
        let result = res.resultStr // needResult  1 
        callback && callback(result)
      }
    })
  },
  wxPay (params, url, elseData, type) {
    chttp.post(url, params).then(res => {
      res.errcode = parseInt(res.errcode)
      if (res.errcode === 200) {
        store.commit('SAVE_ORDER_ID', res.oid)
        wx.chooseWXPay({
          timestamp: res.pay.jspay.timeStamp, // jssdktimestamptimeStampS
          nonceStr: res.pay.jspay.nonceStr, //  32 
          package: res.pay.jspay.package, // prepay_idprepay_id=***
          signType: res.pay.jspay.signType, // 'SHA1''MD5'
          paySign: res.pay.jspay.paySign, // 
          fail: function (res) {
            Vue.$messageBox.alert('', '')
          },
          success: function (res) {
            // 
            if (res.errMsg === 'chooseWXPay:ok') {
              store.commit('CLOSE_PAY_PANEL')
              // 
              if (type === 'blessv1') {
                store.dispatch('nationalQueryOrder', '/c1/bless/wxpay/old/v1')
              } else if (type === 'blessv4') {
                store.dispatch('nationalQueryOrder', '/c1/bless/wxpay/new/v1')
              } else if (type === 'gift') {
                store.dispatch('nationalQueryOrder', '/vod/gift/wxpay')
              } else if (type === 'photo') {
                store.dispatch('nationalQueryOrder', '/c1/photo/wxpay/old/v1')
              } else if (type === 'photov2') {
                store.dispatch('nationalQueryOrder', '/c1/photo/wxpay/new/v1')
              }
              elseData.callback() === 'function' && elseData.callback()
            } else {
              Vue.$messageBox.alert('', '')
            }
          },
          cancel: function (res) {
            // 
            Vue.$messageBox.alert('', '')
          }
        })
      } else if (res.errcode === 52000) {
        Vue.$toast(res.errmsg)
      } else {
        Vue.$toast('')
      }
    }).catch(err => {
      console.log(err)
      Vue.$toast('')
    })
  },
  startRecord (e) {
    wx.startRecord()
  },
  stopRecord (_this) {
    wx.stopRecord({
      success (res) {
        return wx.translateVoice({
          localId: res.localId, // Id
          isShowProgressTips: 1, // 1
          success: function (res) {
            _this.setWord(res.translateResult)
          },
          fail: function () {
            alert('')
          }
        })
      }
    })
  },
  getLocation (that, callback) {
    wx.getLocation({
      type: 'wgs84', // wgs84gpsopenLocation'gcj02'
      success: function (res) {
        that.latitude = res.latitude // 90 ~ -90
        that.longitude = res.longitude // 180 ~ -180
        typeof callback === 'function' && callback()
      },
      fail: function (err) {
        if (err.errMsg.indexOf('getLocation:fail') >= 0) {
          location.reload()
        } else {
          console.log(err)
        }
      },
      cancel: function () {
        typeof callback === 'function' && callback()
      }
    })
  }
}

export default weixin
