/*
 *  ()
 * scrollresize
 * 
 * throttle(callback, 1000) // 
 * throttle(callback, 1000, {leading: false}) // 1000
 * throttle(callback, 1000, {trailing: false}) // 
 */
function throttle (callback, wait = 300, opts = {}) {
  let context, args, result
  let timeout = null
  let previous = 0

  function laterFn () {
    previous = opts.leading === false ? 0 : Date.now()
    timeout = null
    result = callback.apply(context, args)
    context = args = null
  }

  function throttled () {
    context = this
    args = arguments
    const now = Date.now()
    // console.log(`time is ${now}`)
    if (!previous && opts.leading === false) previous = now
    const remaining = wait - (now - previous)
    if (remaining <= 0 || remaining > wait) {
      clearTimeout(timeout)
      timeout = null
      previous = now
      result = callback.apply(context, args)
      context = args = null
    } else if (!timeout && opts.trailing !== false) {
      timeout = setTimeout(laterFn, remaining)
    }
    return result
  }
  throttled.cancel = function () {
    clearTimeout(timeout)
    previous = 0
    timeout = context = args = null
  }
  return throttled
}

export default throttle
